* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  border: none;
}
:global html, :global body {
    height: 100%;
    width: 100%;
    overflow: hidden;
  }
@media only screen and (min-width: 769px) {
  :global html, :global body {
      margin: auto;
      width: 64%;
  }
}
:global body {
    font-family: -apple-system, 'Helvetica Neue', Helvetica, Arial,
      'PingFang SC', 'Hiragino Sans GB', 'WenQuanYi Micro Hei',
      'Microsoft Yahei', sans-serif;
    font-size: 14px;
    line-height: 1.42858;
    color: #333;
    background-color: #fff;
    -webkit-font-smoothing: antialiased;
}
:global button {
    cursor: pointer;
}
:global a {
    text-decoration: none;
}
:global i {
    cursor: pointer;
    display: inline-block;
    width: 30px;
    height: 30px;
}
:global li {
    list-style: none;
}
:global .clearfix:before, :global .clearfix:after {
    display: table;
    content: ' ';
}
:global .clearfix:after {
    clear: both;
}
:global .clearfix {
    *zoom: 1;
}
:global .online {
    width: 200px;
    margin: 3px auto;
    border-radius: 4px;
    text-align: center;
    background-color: #eceff1;
    color: gray;
    font-size: 50%;
}
:global #root, :global .App, :global .App > div {
    width: 100%;
    height: 100%;
  }
:global(.App) {
  position: relative;
  width: 100%;
  height: 100%;
}



